.header {
  display: flex;
  width: 1440px;
  height: 64px;
  flex-shrink: 0;
  background: var(--gray-white, #FFF);
  box-shadow: 0px 1px 0px 0px #E2E6E9;
}

.header-logo {
  display: flex;
  height: 24px;
  width: 40px;
  margin: 20px 64px 20px 24px;
}

.header__content-left {
  display: inline-flex;
  align-items: center;
  gap: 64px;
}

a {
  color: var(--gray-secondary, #89939A);
  font-family: Mont;
  font-size: 12px;
  font-weight: 700;
  font-style: normal;
  line-height: 11px;
  letter-spacing: 0.48px;
  text-decoration: none;
  text-transform: uppercase;
}

a:hover {
  color: var(--gray-primary, #313237);
}

.header__content-right {
  display: flex;
  margin-left: 756px;
}

.image {
  display: flex;
  height: 16px;
  width: 16px;
}

.favorits,
.basket {
  display: flex;
  width: 64px;
  height: 64px;
  justify-content: center;
  align-items: center;
  background: var(--gray-white, #FFF);
  box-shadow: -1px 0px 0px 0px #E2E6E9;
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
}
